import Component from 'vue-class-component'
import {Vue} from 'vue-property-decorator'
import LineIndicator from "@/model/indicators/LineIndicator"
import {CriminalData, getData} from "@/shared/loader";
import {CriminalCharts, IndicatorChart} from "@/shared/charts";

@Component({
    components: {
        'v-line-indicator': LineIndicator
    }
})
export default class Home extends Vue {
    private preparingData = true
    private chartTotal: IndicatorChart | undefined

    async mounted() {
        this.preparingData = true
        await CriminalData.fetch()
        this.chartTotal = CriminalCharts.chartTotal(CriminalData.getYearsData(1,'19'))
        this.preparingData = false
    }

}
