    public static chartTotal(dataI1: IndicatorData): IndicatorChart {
        return {
            data: {
                labels: dataI1.labels,
                datasets: [
                    {
                        label: ' (.)',
                        fill: false,
                        backgroundColor: '#3498db',
                        borderColor: '#2e86c1',
                        pointRadius: 6,
                        data: dataI1.values.map(value => value / 1000)
                    }
                ]
            },
            options: {
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        },
                        scaleLabel: {
                            display: true,
                            labelString: ' (.)'
                        }
                    }]
                },
                legend: {
                    display: false,
                    position: "bottom"
                },
                title: {
                    display: true,
                    text: '  '
                },
                events: ['mousemove'],
                onHover: (event: any, chartElement: any) => {
                    event.target.style.cursor = chartElement[0] ? 'pointer' : 'default';
                }
            }
        }
    }

